/* MdContent.css */

/* Markdown 容器整体 */
.mdContent {
  line-height: 1.6;
  white-space: pre-line; /* 支持 \n 换行 */
  font-size: 1rem;
  color: #333;
}

/* 段落 */
.mdContent p {
  margin: 0.5rem 0;
}

/* 标题 */
.mdContent h1 {
  font-size: 1.8rem;
  margin: 1rem 0 0.5rem;
}
.mdContent h2 {
  font-size: 1.5rem;
  margin: 0.8rem 0 0.4rem;
}
.mdContent h3 {
  font-size: 1.2rem;
  margin: 0.6rem 0 0.3rem;
}

/* 列表 */
.mdContent ul,
.mdContent ol {
  padding-left: 1.5rem;
  margin: 0.5rem 0;
}

/* 链接 */
.mdContent a {
  color: #1e90ff;
  text-decoration: underline;
}

/* 代码块 */
.mdContent pre {
  background-color: #ca4e4e;
  padding: 0.75rem;
  border-radius: 8px;
  overflow-x: auto;
}

.mdContent code {
  background-color: #eee;
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-family: monospace;
}

/* 图片 */
.mdContent img {
  max-width: 100%;
  border-radius: 8px;
  margin: 0.5rem 0;
}
